<script>
	import { within } from '@turf/turf';
	import Map from './Map.svelte';
	import MapSource from './MapSource.svelte';
	import MapLayer from './MapLayer.svelte';
	import MapDraw from './MapDraw.svelte';
	import Panel from './Panel.svelte';
	import Select from './Select.svelte';
	import Loader from './Loader.svelte';
	import { getPlaces, getPostcode, getBoundary, getPoints } from './utils.js';
	
	// Settings
	const mapstyle = 'https://bothness.github.io/ons-basemaps/data/style-omt.json';
	const vector = {
		url: 'https://cdn.ons.gov.uk/maptiles/t9/{z}/{x}/{y}.pbf',
		layer: 'OA_bound_ethnicity',
		id: 'oa11cd'
	}
	
	// Elements
	let map = null;
	let draw = null;
	
	// Data
	let centroids;
	let lookup;
	let places;
	
	// State
	let selectedPlace = null;
	let selected = [];
	let population = 0;
	let hovered = null;
	let mapZoom = null;
	let loaded = false;
	let drawing = false;
	let polygons = 0;
	
	function doSelect(e) {
		let code = e.detail.code;
		if (code == null) {
			selected = [];
			let id = 'boundary';
			if (map.getLayer(id)) {
				map.removeLayer(id);
				map.removeSource(id);
			}
			map.fitBounds([[-5.816, 49.864], [1.863, 55.872]]);
		} else {
			loaded = false;
			// Get geographic boundary of selected place
			let id = 'boundary';
			let boundary;
			getBoundary(code)
			.then(result => {
				// Remove previous boundary from the map
				if (map.getLayer(id)) {
					map.removeLayer(id);
					map.removeSource(id);
				}
			
				// Add new boundary to the map
				map.addSource(id, {
					'type': 'geojson',
					'data': result.geometry
				});
				map.addLayer({
					'id': id,
					'type': 'line',
					'source': id,
					'layout': {},
					'paint': {
						'line-color': 'rgb(26, 94, 150)',
						'line-width': 2
					}
				}, 'boundary_country');
				
				// Select OAs within boundary
				if (centroids) {
					let points = within(centroids, result.geometry);
					selected = points.features.map(d => d.properties.id);
				} else {
					console.log('Centroids not loaded.');
				}

				loaded = true;
				// Fit map to boundary
				map.fitBounds(result.bounds, { padding: 20 });
			});
		}
	}
	
	function clearData() {
		selected = [];
		population = 0;
	}

	function drawEdit() {
		if (!drawing) {
			draw.changeMode('draw_polygon');
			drawing = true;
		} else {
			draw.changeMode('simple_select');
			drawing = false;
		}
	}
	function drawDelete() {
		draw.deleteAll();
		polygons = 0;
		draw.changeMode('simple_select');
	}
	
	getPlaces().then(data => {
		places = data;
		if (centroids && lookup) {
			loaded = true;
		}
	});
	
	getPoints().then(data => {
		centroids = data.geometry;
		lookup = data.lookup;
		if (places) {
			loaded = true;
		}
	});
	
</script>

<style>
	:global(body) {
		margin: 0;
		padding: 0;
	}
	button {
		margin-bottom: 0;
	}
</style>

<Panel>
	<h1>Draw your own geography</h1>
	{#if places}
	<Select options={places} bind:selected={selectedPlace} search={true} on:select={doSelect} />
	{:else}
	<Loader small={true}/>
	{/if}
	{#if mapZoom && mapZoom < 8 }
	<p>Zoom in to draw a polygon, select output areas, or choose a pre-defined geography above.</p>
	{:else if mapZoom}
	<p>
		{#if !drawing}
		<button on:click={drawEdit}>Draw polygon</button>
		{:else}
		<button on:click={drawEdit}>Stop drawing</button>
		{/if}
		{#if polygons > 0}<button on:click={drawDelete}>Delete polygons</button>{/if}
		{#if selected.length > 0}<button on:click={clearData}>Clear Output Areas</button>{/if}
	</p>
	{/if}
	<h3>Population {population.toLocaleString()}</h3>
	{#if selected.length > 0}
	<h3>{selected.length.toLocaleString()} output areas selected</h3>
	<p><small>{selected.join(', ')}</small></p>
	{/if}
</Panel>

{#if !loaded}
<Loader height="100vh" width="100vw" position="absolute" bgcolor="rgba(255, 255, 255, 0.7)"/>
{/if}
<Map bind:map={map} style={mapstyle} minzoom={4} maxzoom={14} bind:zoom={mapZoom}>
	<MapSource id="oa" type="vector" url={vector.url} layer={vector.layer} promoteId={vector.id} minzoom={8} >
		{#if lookup}
		<MapLayer id="oa_fill" source="oa" sourceLayer={vector.layer} type="fill" click={true} hover={true} bind:selected={selected} {lookup} bind:population={population} bind:drawing={drawing} {hovered} paint="{{
			'fill-color': ['case',
				['==', ['feature-state','selected'], true], 'rgba(0, 0, 0, 0.2)',
				'rgba(0, 0, 0, 0)'
			],
		}}" order="boundary_state" />
		{/if}
		<MapLayer id="oa_boundary" source="oa" sourceLayer={vector.layer} type="line" paint="{{
			'line-color': ['case',
				['==', ['feature-state','hovered'], true], 'rgb(0, 0, 0)',
				'rgb(128, 128, 128)'
			],
			'line-width': ['case',
				['==', ['feature-state','hovered'], true], 1,
				0.25
			],
		}}" order="boundary_country" />
	</MapSource>
	{#if centroids}
	<MapDraw bind:draw={draw} bind:polygons={polygons} bind:drawing={drawing} bind:selected={selected} bind:loaded={loaded} {centroids}/>
	{/if}
	{#if centroids}
	<!-- Optional section of code to display OA centroids -->
	<MapSource id="centroids" type="geojson" data={centroids} promoteId="id">
		<MapLayer id="centroids" source="centroids" type="circle" paint="{{
			'circle-color':  'rgb(168, 168, 168)',
			'circle-radius': [
				"interpolate", ["linear"], ["zoom"],
				9, 0.5, 14, 2
			]
		}}" minzoom={8} />
	</MapSource>
	{/if}
</Map>